
.item {
    margin-bottom: 35px;
    display: flex;
    color: black;

}

.item img {

    width: 60px;
    height: 60px;
    border-radius: 50%;

}

.item .postText {
    transform: translateY(20px);
    margin-left: 20px;
    font-size: 20px;

}


.item .likesInfo {
    transform: translateY(27px);
    font-size: 12px;
    margin-left: 10px;



}

.item .likesInfo span {

    font-weight: bold;
    color: #f25c66;




}



