* {
    margin: 0;
    padding: 0;
}



.imageWrap {
    width: 100%;
    height: 250px;

}

.profileImage {

    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: 0 -260px;

}

.changeAvatarBtn {
    transform: scale(1);
    position: absolute;
    right: 330px;
    top: 310px;
}



.descriptionBlock {


}



.avatarImage {
    transform: translateY(-55px);
    display: inline-block;
    width: 120px;
    height: 120px;
    background: #000;
    /*border-radius: 50%;*/
    margin-right: 20px;
    margin-left: 25px;


}


.descriptionWrapper {
    margin-top: 20px;
    display: inline-block;
    width: 750px;


}

.descriptionWrapper h3 {
    font-size: 22px;

}

.descriptionWrapper .lastVisit {
    margin-top: 10px;
    font-size: 12px;
    font-style: italic;


}

.descriptionWrapper p {
    margin-bottom: 20px;
    font-size: 18px;

}



.statusBar {
    margin-top: 5px;


}


.statusBarText {
    font-size: 16px;
    opacity: 0.5;
    cursor: pointer;

}

.statusBarInput {
    font-size: 16px;
    width: 250px;
    height: 25px;
    padding-left: 5px;
    border: none;
    cursor: pointer;

}

.statusBarInput:focus {
    cursor: text;
    outline-color: #F2856D;

}

.contactsForm {
    margin-left: 35px;
}

.contactsForm div {
    margin-top: 10px;
}


.socialContacts {
    margin-left: 20px;
    font-weight: 400;
    font-size: 16px;
    /*text-transform: capitalize;*/
}

.socialContacts span {
    color: lightskyblue;
    cursor: pointer;
}