.postsBlock {
    margin-left: 25px;
    margin-top: 30px;
    padding: 10px;
}

.postsBlock h3 {
    margin-bottom: 20px;
}

.posts {
    margin-top: 50px;
}

.btn {
    width: 130px;
    height: 50px;
    border: none;
    background: #F2856D;
    color: #000;
    margin-top: 10px;
    text-transform: uppercase;
    font-size: 10px;
    font-weight: bold;
    transition: all 0.2s;


}

.btn:hover, .btn:focus {
    background: #ebcfab;
    outline: none;
}

.textArea span {
    color: #0c5460;
}